import { useEffect } from 'react';
import { useMemo } from 'react';
import { useDropzone } from 'react-dropzone';

const baseStyle = {
  cursor: 'pointer',
  flex: 1,
  height: "100%",
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  borderWidth: 2,
  borderRadius: 2,
  borderColor: '#eeeeee',
  borderStyle: 'dashed',
  backgroundColor: '#fafafa',
  color: '#bdbdbd',
  outline: 'none',
  transition: 'border .24s ease-in-out'
};

const focusedStyle = {
  borderColor: '#2196f3'
};

const acceptStyle = {
  borderColor: '#00e676'
};

const rejectStyle = {
  borderColor: '#ff1744'
};

const DropZone = (props) => {
  const {
    onDrop,
    children,
    ...other
  } = props;

  const {
    acceptedFiles,
    getRootProps,
    getInputProps,
    isFocused,
    isDragAccept,
    isDragReject
  } = useDropzone({ accept: {
    "video/mp4": [".mp4"], 
    "video/mpeg": [".mp4"],
    "image/png": [".png"],
    "image/jpeg": [".jpg"],
    "image/gif": [".gif"],
}, noClick: false, maxFiles: 1 });

  useEffect(() => {
    if (acceptedFiles.length) {
      onDrop && onDrop(acceptedFiles);
    }
  }, [acceptedFiles]);

  const style = useMemo(() => ({
    ...baseStyle,
    ...(isFocused ? focusedStyle : {}),
    ...(isDragAccept ? acceptStyle : {}),
    ...(isDragReject ? rejectStyle : {})
  }), [
    isFocused,
    isDragAccept,
    isDragReject
  ]);

  return (
    <div className="container" {...other}>
      <div {...getRootProps({ style })}>
        <input {...getInputProps()}/>
        {children}
      </div>
    </div>
  );
}

export default DropZone;