import React, { useEffect, useState } from 'react'
import css from "../css/header.module.css";
import StorageProgress from "../util/StorageProgress";
import { useNavigate } from 'react-router-dom';
import SideMenu from '../util/SideMenu';
import { useWindowSize } from '../util/WindowUtil';

const Header = ({ name, removeCookie, storage }) => {
    const navigate = useNavigate();
    const handleClick = (param) => {
        navigate("/Monooki" + param)
    }
    const [width, height] = useWindowSize();

    return (
        <>
            <div className={css.Field}>
                <div className={css.Logo} onClick={() => handleClick("/")}>水越家の物置</div>

                {width <= 1100 ? <SideMenu name={name} storage={storage} removeCookie={removeCookie} /> :
                    <div className={css.Menu}>
                        <div className={css.Name}>{name} 様</div>
                        <div>
                            <StorageProgress width={"200px"} total={storage.total} free={storage.free} usage={storage.usage} />
                        </div>
                        {
                            name !== "ゲスト" ?
                                <>
                                    <div className={css.Link} onClick={() => handleClick("/posts")}>投稿一覧</div>
                                    <div className={css.Link} onClick={() => handleClick("/upload")}>アップロード</div>
                                </>
                                : <></>
                        }
                        <div className={css.Link} onClick={() => removeCookie("data")}>ログアウト</div>
                    </div>
                }
            </div>
        </>

    )
}

export default Header