export const fetchData = async (url, method, body) => {
    let f = body === undefined ? await fetch(url, {
        method: method,
        headers: {
            "Content-Type": "application/json;charset=utf-8",
        },
    }).then(res=> res.json()) : await fetch(url, {
        method: method,
        headers: {
            "Content-Type": "application/json;charset=utf-8",
        },
        body: body
    }).then(res=> res.json());
    return f;
}

export const fetchForm = async (url, form) => {
    return await fetch(url, {
        method: "POST",
        body: form
    }).then(res=> res.json())
}

export const fetchFile = async (url, method, body) => {
    let f = body === undefined ? await fetch(url, {
        method: method,
        headers: {
            "Content-Type": "application/octet-stream",
        },
    }).then(res=> res.blob()) : await fetch(url, {
        method: method,
        headers: {
            "Content-Type": "application/octet-stream",
        },
        body: body
    }).then(res=> res.blob());
    return f;
}