import React from 'react'
import FileBox from "./FileBox"

const TopPage = ({filter, setFilter, files, url}) => {
  return (
    <div>
        <div style={{ textAlign: "right", marginRight: "30px" , marginBottom: "20px"}}>
        <label style={{ marginLeft: "10px" }} onClick={() => setFilter("all")}>
          <input type="radio" value="all" checked={filter === "all"} onChange={(e)=> setFilter(e.target.value)}/>
          すべて
        </label>
        <label style={{ marginLeft: "10px" }} onClick={() => setFilter("image")}>
          <input type="radio" value="image" checked={filter === "image"} onChange={(e)=> setFilter(e.target.value)}/>
          画像のみ
        </label>
        <label style={{ marginLeft: "10px" }} onClick={() => setFilter("video")}>
          <input type="radio" value="video" checked={filter === "video"} onChange={(e)=> setFilter(e.target.value)}/>
          動画のみ
        </label>
      </div>
      <div style={{ display: 'flex', flexWrap: "wrap", flexDirection: "row", justifyContent: "flex-start",margin: "0 auto", width: "80%" }}>
        {filter !== "all" ?
          files.filter(file => filter === "video" ? String(file.mimetype).includes("video") && file.error === undefined
            : String(file.mimetype).includes("image") && file.error === undefined).map(file => <FileBox url={url} data={file} key={file.filepath} />)
          : files.map(file => <FileBox url={url} data={file} key={file.filepath} />)}
      </div>
    </div>
  )
}

export default TopPage