import React, { useRef, useState } from 'react'
import DropZone from '../util/DropZone';
import css from "../css/upload.module.css";
import Loading from '../util/Loading';
import { fetchForm } from '../util/Fetch';
import { redirect, useNavigate } from 'react-router-dom';

const Upload = ({ id, url, setSuccessUpload }) => {

  const [fileUrl, setFileUrl] = useState();
  const [file, setFile] = useState();
  const [upload, setUpload] = useState(false);
  const [error, setError] = useState();
  const fileRef = useRef(null);
  const navigate = useNavigate();
  const onDrop = (acceptedFiles) => {
    // Do something with the files
    setFileUrl(URL.createObjectURL(acceptedFiles[0]))
    setFile(acceptedFiles[0])
  };

  const fileUpload = () => {
    if (file === undefined) {
      setError("ファイルが選択されていません")
      return;
    }
    if (upload) {
      setError("アップロード中のためアップロードできません")
      return;
    }
    setUpload(true);
    let f = new FormData()
    f.append("id", id)
    f.append("file", file)
    fetchForm(url + "/upload/", f).then(res => {
      setUpload(false)
      if (res.result !== undefined) {
        alert(res.result)
        navigate("/Monooki/")
        window.location.reload();
      } else if (res.error) {
        alert("エラーが発生しました")
      }
      setError()
      setFile()
      setFileUrl()
    })
  }
  return (
    <div className={css.UploadField}>
      <DropZone onDrop={onDrop} style={{ backgroundColor: "black", width: "700px", height: "350px" }}>
        <div style={{ position: "absolute",  top: "50%", left: "50%", transform: "translate(-50%, -50%)" }}>
          {
            fileUrl === undefined && (
              <span>ファイルをドロップまたはタップで選択してください</span>
            )
          }
          {
            fileUrl !== undefined &&
            <div key={fileUrl?.toString()} className="border border-dark m-2">
              {!String(file.type).includes("video") ?
                <img src={fileUrl} alt="" style={{ maxWidth: 320, maxHeight: 250 }} />
                :
                <video src={fileUrl} style={{ maxWidth: 320, maxHeight: 250 }} controls />
              }
            </div>
          }
        </div>
      </DropZone>
      <div style={{width: "80%", marginTop: "30px", display:"flex" , justifyContent: "center"}}>
        {upload ? <Loading /> : <button className={css.UploadButton} onClick={() => fileUpload()}>アップロード</button>}
        {error ? error : ""}
      </div>

    </div>
  )
}

export default Upload