
import React from 'react'
import { stack as Menu } from 'react-burger-menu'
import StorageProgress from "../util/StorageProgress";
import { useNavigate } from 'react-router-dom';
import css from "../css/header.module.css";

const SideMenu = ({ name, storage, removeCookie }) => {

  const navigate = useNavigate();
  const handleClick = (param) => {
    navigate("/Monooki" + param)
  }
  var styles = {
    bmBurgerButton: {
      position: 'fixed',
      width: '36px',
      height: '30px',
      left: "90%",
      top: '36px'
    },
    bmBurgerBars: {
      background: '#373a47'
    },
    bmBurgerBarsHover: {
      background: '#a90000'
    },
    bmCrossButton: {
      height: '24px',
      width: '24px'
    },
    bmCross: {
      background: '#bdc3c7'
    },
    bmMenuWrap: {
      marginTop: "-53px",
      position: 'fixed',
      height: '100%'
    },
    bmMenu: {
      background: '#373a47',
      padding: '2.5em 1.5em 0',
      fontSize: '1.15em'
    },
    bmMorphShape: {
      fill: '#373a47'
    },
    bmItemList: {
      color: '#b8b7ad',
      padding: '0.8em'
    },
    bmItem: {
      display: 'block'
    },
    bmOverlay: {
      background: 'rgba(0, 0, 0, 0.3)'
    }
  }

  return (
    <div id="outer-container">
      <Menu styles={styles} pageWrapId={"page-wrap"} outerContainerId={"outer-container"} itemListClassName={css.Link} right>
        <main id="page-wrap">
          <div className={css.Name} style={{ marginBottom: "40px", fontSize: "24px" }}>{name} 様</div>
          <div>
            <StorageProgress width={"200px"} total={storage.total} free={storage.free} usage={storage.usage} />
          </div>
          {
            name !== "ゲスト" ?
              <>
                <div className={css.Link} onClick={() => handleClick("/posts")}>投稿一覧</div>
                <div className={css.Link} onClick={() => handleClick("/upload")}>アップロード</div>
              </>
              : <></>
          }
          <div className={css.Link} onClick={() => removeCookie("data")}>ログアウト</div>
        </main>
      </Menu>
    </div>
  )
}

export default SideMenu